.toggle-checkbox {
  height: 0;
  width: 0;
  visibility: hidden;
}

.toggle-label {
  position: relative;
  bottom: 125px;
  left: 440px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
  width: 150px;
  height: 60px;
  background-color: rgb(250, 0, 0);
  border-radius: 100px;
}

.toggle-label .toggle-button {
  content: "";
  position: absolute;
  top: 2px;
  left: 2px;
  width: 45px;
  height: 45px;
  border-radius: 45px;
  transition: 0.2s;
  border: 6px double black;
  background-color: rgb(9, 228, 252);
  background-image: linear-gradient(
    to top left,
    rgba(0, 0, 0, 0.2),
    rgba(0, 0, 0, 0.1) 30%,
    rgba(62, 11, 244, 0.7)
  );
  box-shadow: inset 4px 4px 4px rgba(255, 255, 255, 0.6),
    inset -4px -4px 3px rgba(0, 0, 0, 0.6);
  outline: none;
}

.toggle-checkbox:checked + .toggle-label .toggle-button {
  left: calc(100% - 1px);
  transform: translateX(-100%);
}

.toggle-label:active .toggle-button {
  width: 50px;
}
